<template>
  <div class="playlist-detail" v-loading="pageLoading">
    <div class="head-wrap" v-if="detail.name">
      <div class="img-wrap">
        <img v-lazy="detail.coverImgUrl + '?param=300y300'" alt="img" />
      </div>
      <div class="content">
        <div class="title">{{ detail.name }}</div>
        <div class="creater">
          <img :src="detail.creator.avatarUrl + '?param=30y30'" alt="creater" />
          {{ detail.creator.nickname }}
          <span class="create-time">{{ detail.createTime | dayFormat}} </span>
        </div>
        <!-- <div class="btns">
          <el-button type="primary" round></el-button>
        </div> -->
        <div class="desc-wrap">
          <div class="tags">:{{ formatTags(detail.tags) }}</div>
          <div class="tags">
            {{ detail.trackCount }} {{ dynamic.playCount }}
          </div>
          <div class="desc">{{ detail.description }}</div>
        </div>
      </div>
    </div>
    <div class="tabs-wrap">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <div class="title-wrap">
            <div class="order"></div>
            <div class="play"></div>
            <div class="name"></div>
            <div class="singer"></div>
            <div class="album"></div>
            <div class="time"></div>
          </div>
          <div class="song-list">
            <SonglistCard
            :index="index"
            :name="item.name"
            :ar="item.ar"
            :al="item.al"
            :dt="item.dt"
            v-for="(item,index) in detail.tracks"
            :key="index"
            @click.native="playMusic(item.id)"
            />
          </div>
        </el-tab-pane>
        <el-tab-pane
          :label="'(' + dynamic.commentCount + ')'"
          name="second"
        >
          <div class="comment-list-wrap" v-loading="commentLoading">
            <div class="hot" v-if="comment.hotComments">
              <div class="title"></div>
              <div class="comment-wrap">
                <CommentCard
                :avatarUrl="item.user.avatarUrl"
                :nickname="item.user.nickname"
                :content="item.content"
                :beReplied="item.beReplied"
                :time="item.time"
                :likedCount="item.likedCount"
                v-for="item in comment.hotComments"
                :key="item.commentId"
                />
              </div>
            </div>
            <div class="latest" v-if="comment.comments">
              <div class="title">({{ dynamic.commentCount }})</div>
              <div class="comment-wrap">
                <CommentCard
                :avatarUrl="item.user.avatarUrl"
                :nickname="item.user.nickname"
                :content="item.content"
                :beReplied="item.beReplied"
                :time="item.time"
                :likedCount="item.likedCount"
                v-for="item in comment.comments"
                :key="item.commentId"
                />
              </div>
            </div>
          </div>
          <el-pagination
            background
            layout="prev, pager, next"
            :page-size="50"
            :total="total"
            @current-change="handlePlaylistsPage"
            v-if="total"
            >
          </el-pagination>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import CommentCard from '../components/CommentCard'
import SonglistCard from '../components/SonglistCard'
import {request} from '../utils/request'
export default {
  data() {
    return {
      //
      // /playlist/detail?id=6815249151
      // : ,  , 
      // id,  (,)
      //trackIdstracks 
      // trackIds  song/detail 
      detail: {},
      //
      // /playlist/detail/dynamic?id=6815249151
      dynamic: {},
      //
      //  /comment/playlist?id=6815249151&limit=50&offset=0
      comment: {},
      //
      activeName: 'first',
      //
      total:0,
      //
      pageLoading:false,
      //
      commentLoading:false
    }
  },
  mounted() {
    //
    this.$bus.$on('updatePlaylistDetail',() => {
      //
      this.total = 0
      this.activeName = 'first'
      //
      this.$bus.$emit('goTop')
      this.getDetail(this.$route.params.id)
      this.getDynamic(this.$route.params.id)
    })
  },
  activated() {
    //
    this.total = 0
    this.activeName = 'first'
    //
    this.$bus.$emit('goTop')
    this.getDetail(this.$route.params.id)
    this.getDynamic(this.$route.params.id)
    //
    // this.getComment(this.$route.params.id,1)
  },
  deactivated() {
    this.detail = {}
    this.comment = {}
  },
  methods: {
    //
    handleClick(tab) {
      //
      if(tab.name === 'second') {
        if(JSON.stringify(this.comment) == '{}') {
          this.getComment(this.$route.params.id,1)
        }
      }
    },
    //
    async getDetail(id) {
      const {data} = await request({
        method:'get',
        url:'/playlist/detail',
        params: {
          id
        }
      })
      if(data.code != 200) {
        alert('')
        return
      }
      this.detail = data.playlist
    },
    //
    async getDynamic(id) {
      this.pageLoading = true
      const {data} = await request({
        method:'get',
        url:'/playlist/detail/dynamic',
        params: {
          id
        }
      })
      if(data.code != 200) {
        alert('')
        return
      }
      this.dynamic = data
      this.pageLoading = false
    },
    //
    async getComment(id,curPage) {
      this.commentLoading = true
      const {data} = await request({
        method:'get',
        url:'/comment/playlist',
        params: {
          id,
          limit:50,
          offset:(curPage - 1)*50
        }
      })
      if(data.code != 200) {
        alert('')
        return
      }
      this.comment = data
      this.total = data.total
      this.commentLoading = false
    },
    //
    handlePlaylistsPage(val) {
      //
      this.getComment(this.$route.params.id,val)
      //
      this.$bus.$emit('goTop')
    },
    playMusic(id) {
      //
      if(this.playingMusicId === id) {
        this.$message({
          message:''
        })
        return
      }
      //vuex
			this.$store.dispatch('playMusic',id)
      //vuex
			this.$store.commit('UPDATE_PLAYLIST',this.detail.tracks)
      //
      const curSong = this.detail.tracks.filter(item => {
        return item.id === id
      })
      //vuex
      this.$store.dispatch('addSongToHistory',curSong[0])
      //
      this.$store.commit('CHANGE_PLAYING_VALUE',true)
    }
  },
  computed:{
    formatTags() {
      return function(tags) {
        let a = ''
        tags.forEach((item,i) => {
          if(i == 0) {
            a += item
          }else {
            a += '/' + item
          }
        })
        return a
      }
    }
  },
  beforeDestroy() {
    //
    this.$bus.$off('updatePlaylistDetail')
  },
  components: {
    CommentCard,
    SonglistCard
  }
}
</script>

